@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;overflow:hidden;background-position:center;background-size:cover}.menu-item.large{height:380px}@media screen and (max-width: 800px){.menu-item.large{height:200px}}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:0.9}.menu-item.large{height:380px}@media screen and (max-width: 800px){.menu-item.large{height:200px}}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-size:cover;background-position:center}.menu-item .content{height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid black;background-color:white;opacity:0.7;position:absolute}.menu-item .content .title{font-weight:bold;margin:0 6px 0;font-size:22px;color:#4A4A4A}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}@media screen and (max-width: 800px){.menu-item{height:200px}}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

.custom-button{min-width:165px;width:auto;height:50px;display:flex;justify-content:center;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-log-in{background-color:#4285F4;color:white}.custom-button.google-log-in:hover{background-color:#357AE8;border:none}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:none}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5}.cart .empty-cart-message{margin:50px auto;font-size:18px}.cart .cart-items{height:240px;display:flex;flex-direction:column;overflow:scroll;overflow-x:hidden}.cart button{margin-top:auto}

.cart-icon-container{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon-container .cart-icon{width:24px;height:24px}.cart-icon-container #item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}

* {
	box-sizing: border-box;
}

body {
	padding: 20px 60px;
	font-family: 'Open Sans Condensed', sans-serif;
}

a {
	text-decoration: none;
	color: black;
}

@media screen and (max-width: 800px) {
	body {
		padding: 10px;
	}
}
body {
	margin: 0;
	font-family: 'Open Sans Condensed', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
